<template>
  <div class="w-full justify-center">
    <div class="m-3">
      현재는 개발중인 상태입니다.<br />
      서비스가 런칭하면 모든기록은 삭제할 예정입니다.<br />
      아이디 암호는 버리는 느낌으로 대충 입력하세요.<br />
      암호길이는 4자 까지로 제한했습니다.
    </div>
    <div class="flex justify-center m-3">
      <div class="w-96">
        <div class="flex m-1 justify-center items-center">
          <div class="inline-flex p-1 text-right w-24">아이디:</div>
          <input
            class="inline-flex p-1 border-gray-600 border-2 rounded-md"
            v-model="id"
          />
        </div>

        <div class="flex m-1 justify-center items-center mt-4 bg-gray-300">
          <div class="inline-flex p-1 text-right w-24">암호</div>
          <input
            class="inline-flex p-1 border-gray-600 border-2 rounded-md"
            type="password"
            maxlength="4"
            v-model="pw"
          />
        </div>

        <div class="flex m-1 justify-center items-center">
          <div class="inline-flex p-1 text-right w-24">암호 확인</div>
          <input
            class="inline-flex p-1 border-gray-600 border-2 rounded-md"
            type="password"
            maxlength="4"
            v-model="pwConfirm"
          />
        </div>

        <div class="flex mt-3 justify-center items-center">
          <div class="inline-flex text-center text-red-600 font-bold">
            {{ errorMessage }}
          </div>
        </div>

        <div class="flex justify-center mt-3">
          <button
            type="button"
            class="w-32 p-1 rounded-md"
            :class="buttonClass()"
            @click="onSignup()"
          >
            가입
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      id: "",
      pw: "",
      pwConfirm: "",
      signupLabel: "가입",
      waitResponse: false,
      csrfToken: "",
      errorMessage: "",
      errorMessageByNetwork: "",
    };
  },
  methods: {
    buttonEnable() {
      if (this.id.length < 4) {
        this.errorMessage = "아이디 4자 이상 필요합니다.";
        return false;
      }
      if (this.pw.length < 1) {
        this.errorMessage = "암호 입력이 필요 합니다.";
        return false;
      }
      if (this.pw != this.pwConfirm) {
        this.errorMessage = "암호를 확인해주세요.";
        return false;
      }
      if (this.waitResponse) {
        this.errorMessage = "";
        return false;
      }

      if (this.errorMessageByNetwork != "") {
        this.errorMessage = this.errorMessageByNetwork;
      } else {
        this.errorMessage = "";
      }
      return true;
    },
    buttonClass() {
      return this.buttonEnable() ? "bg-yellow-300" : "bg-gray-300";
    },
    async onSignup() {
      if (!this.buttonEnable() || this.waitResponse) {
        return;
      }

      const reqData = {
        api: "signup",
        id: this.id,
        pw: this.pw,
      };

      this.errorMessageByNetwork = "";
      this.waitResponse = true;
      const res = await this.$store.callAsync(reqData);
      this.waitResponse = false;

      if (res.code != 100) {
        console.log("signup ng", res.message);
        this.errorMessageByNetwork = res.message;
        return;
      }

      console.log("signup ok", res);
      this.$store.state.user = res.user;
      this.$router.push("/welcome");
    },
  },
};
</script>
